body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body {
  background: url(/socialApp/static/media/sky.2e047091.jpg);
  font-family: "Roboto Mono", monospace;
  overflow: hidden; }

.App {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto; }
  .App .notes_and_form {
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0; }
    .App .notes_and_form .current_user span {
      color: white; }
    .App .notes_and_form .current_user button {
      margin-left: 1rem;
      border: none;
      height: 25px;
      padding: 0 15px;
      cursor: pointer;
      border-radius: 4px; }
      .App .notes_and_form .current_user button:hover {
        background: grey; }
    .App .notes_and_form .noteForm {
      margin: 1rem auto; }
      .App .notes_and_form .noteForm input {
        width: 500px;
        background: rgba(75, 73, 73, 0.959);
        height: 30px;
        color: white;
        border: none;
        padding-left: 1rem; }
      .App .notes_and_form .noteForm button {
        background: rgba(13, 92, 13, 0.986);
        border: none;
        height: 32px;
        padding: 0 15px;
        cursor: pointer;
        color: white; }
    .App .notes_and_form .notes_box {
      background: rgba(56, 55, 55, 0.7);
      width: 600px;
      height: 80vh;
      overflow-y: scroll;
      margin: 0.5rem auto; }
      .App .notes_and_form .notes_box .note {
        margin: 1rem;
        display: flex; }
        .App .notes_and_form .notes_box .note .far {
          font-size: 35px;
          color: white; }
        .App .notes_and_form .notes_box .note .email_and_text {
          margin-left: 0.5rem;
          border-radius: 4px;
          background: rgba(255, 255, 255, 0.527);
          max-width: 90%;
          text-align: start;
          display: block; }
          .App .notes_and_form .notes_box .note .email_and_text span {
            font-size: 21px;
            padding: 2px 5px; }
          .App .notes_and_form .notes_box .note .email_and_text p {
            padding: 0 0.5rem;
            word-wrap: break-word; }
  .App .form_block {
    background: white;
    max-width: 290px;
    position: relative;
    top: 200px;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    border: 5px solid white;
    border-radius: 4px;
    opacity: 0.9; }
    .App .form_block #title {
      background: grey;
      padding: 0.5rem;
      color: white; }
    .App .form_block .body form {
      display: flex;
      flex-direction: column;
      text-align: start; }
      .App .form_block .body form span {
        margin: 0 0.4rem;
        font-size: 12px;
        margin-top: 1rem;
        color: rgba(128, 128, 128, 0.904); }
    .App .form_block .body .buttons {
      display: flex;
      justify-content: space-between;
      margin: 0.4rem 0.4rem; }
      .App .form_block .body .buttons .loginBtn {
        width: 70px;
        cursor: pointer;
        padding: 2px;
        font-size: 12px; }
      .App .form_block .body .buttons .registerBtn {
        width: 70px;
        cursor: pointer;
        padding: 2px;
        font-size: 12px; }

